import React, { useState } from "react";

const Filter = ({ onSearch }) => {
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [type, setType] = useState("");
  const [category, setCategory] = useState("");

  const handleSearch = () => {
    onSearch({ startDate, endDate, type, category });
  };

  return (
    <div className="bg-gray-700 p-5 rounded-lg shadow-lg">
      <h3 className="text-lg font-semibold text-gray-200 mb-3"> </h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/*   */}
        <div>
          <label className="block text-gray-300 text-sm font-medium mb-1"> </label>
          <input
            type="date"
            value={startDate}
            onChange={(e) => setStartDate(e.target.value)}
            className="w-full p-2 bg-gray-800 border border-gray-600 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400 text-white"
          />
        </div>

        {/*   */}
        <div>
          <label className="block text-gray-300 text-sm font-medium mb-1"> </label>
          <input
            type="date"
            value={endDate}
            onChange={(e) => setEndDate(e.target.value)}
            className="w-full p-2 bg-gray-800 border border-gray-600 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400 text-white"
          />
        </div>

        {/*   */}
        <div>
          <label className="block text-gray-300 text-sm font-medium mb-1"></label>
          <select
            value={type}
            onChange={(e) => setType(e.target.value)}
            className="w-full p-2 bg-gray-800 border border-gray-600 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400 text-white"
          >
            <option value=""></option>
            <option value="income"></option>
            <option value="expense"></option>
          </select>
        </div>

        {/*   */}
        <div>
          <label className="block text-gray-300 text-sm font-medium mb-1"></label>
          <input
            type="text"
            value={category}
            onChange={(e) => setCategory(e.target.value)}
            placeholder=": , "
            className="w-full p-2 bg-gray-800 border border-gray-600 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400 text-white"
          />
        </div>
      </div>

      {/*   */}
      <div className="mt-4 flex justify-end">
        <button
          onClick={handleSearch}
          className="bg-blue-500 hover:bg-blue-600 text-white font-semibold py-2 px-4 rounded-md transition"
        >
          
        </button>
      </div>
    </div>
  );
};

export default Filter;
